import React, { Component } from "react";
import SearchBar from 'material-ui-search-bar';
import "./App.css";
import {Hero} from "react-landing-page";
import { Link } from 'react-router-dom';
import Grid from "@material-ui/core/Grid/Grid";

class Search extends Component {
    constructor() {
        super();
        this.state = {
            val: ""
        }
    }

    handleSearch = () => {
        this.props.history.push({
            pathname: "/searchres",
            search: this.state.val
        });
    }

    render() {
        return (
            <Hero
                color="black"
                bg="white"
                backgroundImage="data:image/jpeg;base64,"
            >
                <div className="search-bar">
                    <h1 className={"header"}>Please Enter Employee Last Name</h1>
                    <SearchBar
                        value={this.state.val}
                        onChange={(value) => {this.setState({val: value})}}
                        onRequestSearch={() => this.handleSearch()}
                        style={{margin: '0 auto', maxWidth: 800}}/>
                    <div className="main-search">
                        <Grid container justify="left">
                            <Grid item component={Link}
                                  to="/">
                                &#x2190; Main page
                            </Grid>
                        </Grid>
                    </div>
                </div>
            </Hero>
        )
    }
};

export default Search;
