import React, { Component } from "react";
import {Hero} from "react-landing-page";
import Container from "@material-ui/core/Container/Container";
import CssBaseline from "@material-ui/core/CssBaseline/CssBaseline";
import Grid from "@material-ui/core/Grid/Grid";
import TextField from "@material-ui/core/TextField/TextField";
import Button from "@material-ui/core/Button/Button";
import {Link} from "react-router-dom";
import "./App.css"
import Typography from "@material-ui/core/Typography/Typography";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";

let deleted = false;
let returned = false;

class UpdateEmployee extends Component {
    constructor() {
        super();
        this.state = {
            message: "No modifications made",
            id: "",
            firstName: "",
            lastName: "",
            department: "",
            gender: "",
            hired: "",
            salary: "",
            bonus: "",
            edlevel: "",
            dob: ""
        }
        this.deleteEmp = this.deleteEmp.bind(this);
        this.updateEmployee = this.updateEmployee.bind(this);
    }

    componentDidMount() {
        if(typeof this.props.location.state === 'undefined') {
            returned = true;
            this.setState({
                id: "",
                name: "",
                lastName: "",
                department: "",
                gender: "",
                hired: "",
                salary: "",
                bonus: "",
                edlevel: "",
                dob: ""
            })
        }
        else {
            returned = false;
            deleted = false;
            this.setState({
                id: this.props.location.state.id || "",
                name: this.props.location.state.name,
                lastName: this.props.location.state.lastName,
                department: this.props.location.state.department,
                gender: this.props.location.state.gender,
                hired: this.props.location.state.hired,
                salary: this.props.location.state.salary,
                bonus: this.props.location.state.bonus,
                edlevel: this.props.location.state.edlevel,
                dob: this.props.location.state.dob
            })
        }
    }

    handleFirstNameChange = (event) => {
        this.setState({
            name: event.target.value
        })
    }

    handleLastNameChange = (event) => {
        this.setState({
            lastName: event.target.value
        })
    }

    handleDepartmentChange = (event) => {
        this.setState({
            department: event.target.value
        })
    }

    handleGenderChange = (event) => {
        this.setState({
            gender: event.target.value
        })
    }

    handleHiredChange = (event) => {
        this.setState({
            hired: event.target.value
        })
    }

    handleSalaryChange = (event) => {
        this.setState({
            salary: event.target.value
        })
    }

    handleBonusChange = (event) => {
        this.setState({
            bonus: event.target.value
        })
    }

    handleEdlevelChange = (event) => {
        this.setState({
            edlevel: event.target.value
        })
    }

    handleDateChange = (event) => {
        this.setState({
            dob: event.target.value
        })
    }

    deleteEmp() {
        if(returned) {
            window.alert("Your previous request has been fulfilled. Please choose between New search or View employees options");
        }
        else if(deleted) {
            window.alert("This employee has already been deleted from database. Please choose between New search or View employees options");
        }
        else {
            if (window.confirm("Are you sure you want to delete this employee?")) {
                let resp = fetch("/api/employee/" + this.state.id, {
                    method: 'delete',
                    headers: {
                        "Content-Type": "application/json"
                        // 'Content-Type': 'application/x-www-form-urlencoded',
                    },
                })
                    .then(response => response.json())
                    .catch((error) => console.log(error));
                if (resp.id !== 'undefined') {
                    deleted = true;
                    this.setState({
                        message: "Employee has been deleted from database",
                        id: "",
                        name: "",
                        lastName: "",
                        department: "",
                        gender: "",
                        hired: "",
                        salary: "",
                        bonus: "",
                        edlevel: "",
                        dob: ""
                    })
                }
                else {
                    this.setState({
                        message: "Delete process failed, please try again"
                    })
                }
                return resp;
            }
        }
    }

     async updateEmployee () {
        if(returned) {
            window.alert("Your previous request has been fulfilled. Please choose between New search or View employees options");
        }
        else if(deleted) {
            window.alert("This employee has already been deleted from database. Please choose between New search or View employees options")
        }
        else {
            let response = await fetch("/api/employee", {
                method: "PUT", // *GET, POST, PUT, DELETE, etc.
                mode: "cors", // no-cors, *cors, same-origin
                cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
                credentials: "same-origin", // include, *same-origin, omit
                headers: {
                    "Content-Type": "application/json"
                    // 'Content-Type': 'application/x-www-form-urlencoded',
                },
                redirect: "follow", // manual, *follow, error
                referrerPolicy: "no-referrer", // no-referrer, *client
                body: JSON.stringify(this.state)
            })
                .then((res) => res.json())
                .catch((error) => console.log(error));


            response.id ? this.setState({
                message: "Employee information has been updated"
            }) :  this.setState({
                message: "Update failed. Please try again"
            })
        }
    }

    render() {
        return (
            <Hero
                color="black"
                bg="white"
                backgroundImage="data:image/jpeg;base64,"
            >
                <div className="header">
                    <h1>Please Edit Employee Information Below</h1>
                </div>
                <Container component="main" maxWidth="xs">
                    <CssBaseline />
                    <div className="paper">
                        <form className="form" noValidate>
                            <Grid container spacing={2}>
                                <Grid item xs={12}>
                                    <TextField
                                        variant="outlined"
                                        required
                                        fullWidth
                                        id="name"
                                        value={this.state.name || ""}
                                        label="First Name"
                                        name="name"
                                        autoComplete="name"
                                        onChange={this.handleFirstNameChange}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        variant="outlined"
                                        required
                                        fullWidth
                                        id="name"
                                        value={this.state.lastName || ""}
                                        label="Last Name"
                                        name="name"
                                        autoComplete="name"
                                        onChange={this.handleLastNameChange}
                                        />
                                </Grid>
                                <Grid item xs={12} xs={6}>
                                    <FormControl fullWidth variant="outlined">
                                        <InputLabel htmlFor="outlined-gender-native-simple">Department</InputLabel>
                                        <Select
                                            variant="outlined"
                                            value={this.state.department}
                                            native
                                            label="Department"
                                            inputProps={{
                                                name: 'department',
                                                id: 'outlined-native-simple',
                                            }}
                                            onChange={this.handleDepartmentChange}
                                        >
                                            <option aria-label="None" value="" />
                                            <option>Management</option>
                                            <option>Maintenance</option>
                                            <option>Machinery</option>
                                            <option>Packaging</option>
                                            <option>Processing</option>
                                            <option>Shipment</option>
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={12} xs={6}>
                                    <FormControl fullWidth variant="outlined">
                                        <InputLabel htmlFor="outlined-gender-native-simple">Gender</InputLabel>
                                        <Select
                                            value={this.state.gender || ""}
                                            native
                                            label="Gender"
                                            inputProps={{
                                                name: 'gender',
                                                id: 'outlined-native-simple',
                                            }}
                                            onChange={this.handleGenderChange}
                                        >
                                            <option aria-label="None" value="" />
                                            <option>Male</option>
                                            <option>Female</option>
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <TextField
                                        variant="outlined"
                                        required
                                        fullWidth
                                        type="date"
                                        id="hired"
                                        value={this.state.hired || ""}
                                        label="Date Hired"
                                        name="hired"
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        onChange={this.handleHiredChange}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={3}>
                                    <TextField
                                        variant="outlined"
                                        required
                                        fullWidth
                                        id="salary"
                                        value={this.state.salary || ""}
                                        label="Salary"
                                        name="salary"
                                        autoComplete="salary"
                                        onChange={this.handleSalaryChange}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={3}>
                                    <TextField
                                        variant="outlined"
                                        required
                                        fullWidth
                                        id="bonus"
                                        value={this.state.bonus || ""}
                                        label="Bonus"
                                        name="bonus"
                                        autoComplete="bonus"
                                        onChange={this.handleBonusChange}
                                    />
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <TextField
                                        variant="outlined"
                                        required
                                        fullWidth
                                        id="edlevel"
                                        value={this.state.edlevel || ""}
                                        label="Years of Education"
                                        name="edlevel"
                                        autoComplete="edlevel"
                                        onChange={this.handleEdlevelChange}
                                    />
                                </Grid>
                                <Grid item xs={12}>
                                    <TextField
                                        id="date"
                                        label="Date of birth"
                                        type="date"
                                        className='textField'
                                        value={this.state.dob || ""}
                                        InputLabelProps={{
                                            shrink: true
                                        }}
                                        onChange={this.handleDateChange}
                                    />
                                </Grid>
                            </Grid>
                        </form>
                    </div>
                </Container>
                <div className="mod-buttons">
                    <div className="delete-button">
                        <Button fullWidth
                                variant="contained"
                                color="secondary"
                                onClick={this.deleteEmp}
                                component={Link}
                        >
                            Delete employee
                        </Button>
                    </div>
                    <div>
                        <Button component={Link}
                                fullWidth
                                variant="contained"
                                color="primary"
                                onClick={this.updateEmployee}
                        >Update information
                        </Button>
                    </div>
                </div>
                <Typography style={{ margin: 7 }} variant="body1">
                    Session status: {this.state.message}
                </Typography>
                <Link to="/search">
                    {" "}
                    <Typography align="left">
                        New search
                    </Typography>{" "}
                </Link>
                <Link to={{pathname: "/view",
                        state: {
                           route: "employee"
                        }
                }}>
                    {" "}
                    <Typography align="left">
                        View employees
                    </Typography>{" "}
                </Link>
            </Hero>
        )
    }
}

export default UpdateEmployee;
