import React from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";

import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';

import { Link } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import GroupIcon from "@material-ui/icons/Group";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import {Hero} from "react-landing-page";

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(7),
        display: "flex",
        flexDirection: "column",
        alignItems: "center"
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main
    },
    form: {
        width: "100%", // Fix IE 11 issue.
        marginTop: theme.spacing(3)
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        width: "100%"
    }
}));

export default function AddEmployee() {
    const classes = useStyles();
    const [firstLoad, setLoad] = React.useState(true);

    const [name, setName] = React.useState("");
    const [lastName, setLastName] = React.useState("");
    const [department, setDepartment] = React.useState("");
    const [gender, setGender] = React.useState("");
    const [hired, setHired] = React.useState("2015-01-01");
    const [salary, setSalary] = React.useState("");
    const [bonus, setBonus] = React.useState("");
    const [edlevel, setEdlevel] = React.useState("");
    const [date, setDate] = React.useState("1985-01-01");

    const handleDateChange = event => setDate(event.target.value);
    const handleNameChange = event => setName(event.target.value);
    const handleLastNameChange = event => setLastName(event.target.value);
    const handleDepartmentChange = event => setDepartment(event.target.value);
    const handleGenderChange = event => setGender(event.target.value);
    const handleHiredChange = event => setHired(event.target.value);
    const handleSalaryChange = event => setSalary(event.target.value);
    const handleBonusChange = event => setBonus(event.target.value);
    const handleEdlevelChange = event => setEdlevel(event.target.value);

    const [message, setMessage] = React.useState("Nothing has been saved so far");

    async function sampleFunc(toInput) {

        const response = await fetch("/api/employee", {
            method: "POST", // *GET, POST, PUT, DELETE, etc.
            mode: "cors", // no-cors, *cors, same-origin
            cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
            credentials: "same-origin", // include, *same-origin, omit
            headers: {
                "Content-Type": "application/json"
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            redirect: "follow", // manual, *follow, error
            referrerPolicy: "no-referrer", // no-referrer, *client
            body: JSON.stringify(toInput) // body data type must match "Content-Type" header
        });
        let body = await response.json();
        setMessage(body.id ? "Data sucessfully updated" : "Data update failed");
    }

    const handleSubmit = variables => {
        let now = new Date(date);
        let hiredDate = new Date(hired);
        const toInput = { name, lastName, department, gender, hired: hiredDate.toISOString().slice(0, 10).replace('T', ' '), salary, bonus, edlevel, dob: now.toISOString().slice(0, 10).replace('T', ' ') };
        sampleFunc(toInput);
        setName("");
        setLastName("");
        setDepartment("");
        setGender("");
        setSalary("");
        setBonus("");
        setEdlevel("");
        setHired("2015-01-01");
        setDate("1985-01-01");
    };

    if (firstLoad) {
        // sampleFunc();
        setLoad(false);
    }

    return (
        <Hero
            color="black"
            bg="white"
            backgroundImage="data:image/jpeg;base64,"
            >
            <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <GroupIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Employee Directory
                </Typography>
                <form className={classes.form} noValidate>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="name"
                                value={name}
                                label="First Name"
                                name="name"
                                autoComplete="name"
                                onChange={handleNameChange}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="name"
                                value={lastName}
                                label="Last Name"
                                name="name"
                                autoComplete="name"
                                onChange={handleLastNameChange}
                            />
                        </Grid>
                        <Grid item xs={12} xs={6}>
                            <FormControl fullWidth variant="outlined">
                                <InputLabel htmlFor="outlined-gender-native-simple">Department</InputLabel>
                                <Select
                                    variant="outlined"
                                    value={department}
                                    native
                                    label="Department"
                                    inputProps={{
                                        name: 'department',
                                        id: 'outlined-native-simple',
                                    }}
                                    onChange={handleDepartmentChange}
                                >
                                    <option aria-label="None" value="" />
                                    <option>Management</option>
                                    <option>Maintenance</option>
                                    <option>Machinery</option>
                                    <option>Packaging</option>
                                    <option>Processing</option>
                                    <option>Shipment</option>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} xs={6}>
                            <FormControl fullWidth variant="outlined">
                                <InputLabel htmlFor="outlined-gender-native-simple">Gender</InputLabel>
                                    <Select
                                        variant="outlined"
                                        value={gender}
                                        native
                                        label="Gender"
                                        inputProps={{
                                            name: 'gender',
                                            id: 'outlined-native-simple',
                                        }}
                                        onChange={handleGenderChange}
                                    >
                                    <option aria-label="None" value="" />
                                    <option>Male</option>
                                    <option>Female</option>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="date"
                                label="Date hired"
                                type="date"
                                value={hired}
                                className={classes.textField}
                                InputLabelProps={{
                                    shrink: true
                                }}
                                onChange={handleHiredChange}
                            />
                        </Grid>
                        <Grid item xs={12} sm={3}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="salary"
                                value={salary}
                                label="Salary"
                                name="salary"
                                autoComplete="salary"
                                onChange={handleSalaryChange}
                            />
                        </Grid>
                        <Grid item xs={12} sm={3}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="bonus"
                                value={bonus}
                                label="Bonus"
                                name="bonus"
                                autoComplete="bonus"
                                onChange={handleBonusChange}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="gender"
                                value={edlevel}
                                label="Years of Education"
                                name="years of education"
                                autoComplete="years of education"
                                onChange={handleEdlevelChange}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="date"
                                label="Date of birth"
                                type="date"
                                value={date}
                                className={classes.textField}
                                InputLabelProps={{
                                    shrink: true
                                }}
                                onChange={handleDateChange}
                            />
                        </Grid>
                    </Grid>
                    <Button
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        onClick={handleSubmit}
                    >
                        Save
                    </Button>

                    <Grid container justify="center">
                        <Grid item component={Link}
                              to={{pathname: "/view",
                                  state:
                                      {route: "employee"}}
                              }>
                            View Employee Records
                        </Grid>
                    </Grid>
                </form>
                <Typography style={{ margin: 7 }} variant="body1">
                    Session status: {message}
                </Typography>
            </div>
        </Container>
        </Hero>
    );
}
